import { Injectable, UnauthorizedException } from '@nestjs/common';
import { JwtService } from '@nestjs/jwt';
import * as bcrypt from 'bcrypt';
import { UsersService } from '../../../../apps/api/src/app/users.service'; // Direct import from App
// If the import above fails, we may need to adjust the path or move UsersService to a lib later.
// For now, let's try this direct path.

@Injectable()
export class AuthService {
  constructor(
    private usersService: UsersService,
    private jwtService: JwtService
  ) {}

  // 1. Validate User (Check if email exists and password matches)
  async validateUser(email: string, pass: string): Promise<any> {
    // We need to add a 'findByEmail' method to UsersService in the next step!
    const user = await this.usersService.findByEmail(email);
    
    if (user && (await bcrypt.compare(pass, user.password))) {
      const { password, ...result } = user;
      return result;
    }
    return null;
  }

  // 2. Login (Generate JWT Token)
  async login(user: any) {
    const payload = { username: user.email, sub: user.id, role: user.role };
    return {
      accessToken: this.jwtService.sign(payload),
      user: user
    };
  }
  
  // 3. Register/Sign up helper (Optional but helpful)
  async hashPassword(password: string): Promise<string> {
      return await bcrypt.hash(password, 10);
  }
}